import React from 'react';
import './print.css';

class PrintComponent extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			collectionlist: [],
			newlist: 0,
		};
	}

	render() {
		function addDate(date) {
			var newDate = new Date(Number(date));
			newDate.setDate(date.getDate() + 100);
			let month = newDate.getMonth();
			let date1 = newDate.getDate();
			if (month < 10) {
				month = parseInt(month) + 1;
				month = '0' + month;
			}
			if (date1 < 10) {
				date1 = parseInt(date1);
				date1 = '0' + date1;
			}
			let prevDate = newDate.getFullYear() + '-' + month + '-' + date1;
			return prevDate;
		}

		return (
			<div>
				<p className='h3 w-100 text-align-center d-flex justify-content-center'>
					Abipravi Finance
				</p>
				<div className='overflow-auto'>
					<table
						id='ifmcontentstoprint'
						className='table table-bordered table-hover table-striped border-dark'>
						<thead>
							<tr></tr>
						</thead>
						<tbody>
							{this.props.data
								.sort((a, b) => {
									if (a.id < b.id) return -1;
									if (a.id > b.id) return 1;
									return 0;
								})
								.sort((a, b) => {
									if (a.place_id < b.place_id) return -1;
									if (a.place_id > b.place_id) return 1;
									return 0;
								})
								.map((dt, index) => {
									const date = new Date(dt.loan_date);
									const EndData = addDate(date);
									return (
										<tr key={index} className='print__table'>
											<td
												style={{
													fontSize: '13px',
													padding: '3px',
													width: '60px',
												}}>
												{dt.name}
											</td>
											<td
												style={{
													fontSize: '11px',
													padding: '2px',
													width: '10px',
												}}>
												{dt.loanammount}
											</td>
											<td
												style={{
													fontSize: '11px',
													padding: '2px',
													width: '20px',
												}}>
												{dt.ammount_Balance}
											</td>
											<td
												style={{
													fontSize: '11px',
													padding: '2px',
													width: '50px',
												}}>
												{dt.loan_date}
											</td>
											<td
												style={{
													fontSize: '11px',
													padding: '2px',
													width: '50px',
												}}>
												{EndData}
											</td>
											<td
												style={{
													fontSize: '11px',
													padding: '2px',
													width: '50px',
												}}></td>
											<td
												style={{
													fontSize: '11px',
													padding: '2px',
													width: '50px',
												}}>
												{' '}
											</td>
											<td
												style={{
													fontSize: '11px',
													padding: '2px',
													width: '50px',
												}}>
												{' '}
											</td>
											<td
												style={{
													fontSize: '11px',
													padding: '2px',
													width: '50px',
												}}>
												{' '}
											</td>
											<td
												style={{
													fontSize: '11px',
													padding: '2px',
													width: '50px',
												}}>
												{' '}
											</td>
										</tr>
									);
								})}
						</tbody>
					</table>
				</div>
			</div>
		);
	}
}
export default PrintComponent;
